import React, { useState } from "react";
import Nav from "./Nav";
import "./App.css";
import ItemPage from "./ItemPage";
import { items } from "./static-data";
import CartPage from "./CartPage";

const App = () => {
  const [activeTab, setActiveTab] = useState(0);
  const [cart, updateCart] = useState([]);

  const handleTabChange = index => {
    setActiveTab(index);
  };

  const handleAddToCart = item => {
    updateCart([...cart, item.id]);
  };

  const handleRemoveOne = item => {
    let index = cart.indexOf(item.id);
    updateCart([...cart.slice(0, index), ...cart.slice(index + 1)]);
  };

  const renderContent = () => {
    switch (activeTab) {
      case 0:
        return <ItemPage items={items} onAddToCart={handleAddToCart} />;
      case 1:
        return renderCart();
      default:
        return null;
    }
  };
  const renderCart = () => {
    // Count how many of each item is in the cart
    let itemCounts = cart.reduce((itemCounts, itemId) => {
      itemCounts[itemId] = itemCounts[itemId] || 0;
      itemCounts[itemId]++;
      return itemCounts;
    }, {});
    // Create an array of items
    let cartItems = Object.keys(itemCounts).map(itemId => {
      // Find the item by its id
      let item = items.find(item => item.id === parseInt(itemId, 10));
      // Create a new "item" and add the 'count' property
      return { ...item, count: itemCounts[itemId] };
    });
    return (
      <CartPage
        items={cartItems}
        onAddOne={handleAddToCart}
        onRemoveOne={handleRemoveOne}
      />
    );
  };

  return (
    <div className="App">
      <Nav activeTab={activeTab} onTabChange={handleTabChange} />
      <main className="App-content">{renderContent()}</main>
    </div>
  );
};

export default App;
